import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { RestService } from './rest.service';
import { CotizacionModel } from '../../cotizacion.model';

@Component({
  selector: 'app-add-archivo-cotizacion',
  templateUrl: './add-archivo-cotizacion.component.html',
  styleUrls: ['./add-archivo-cotizacion.component.sass']
})
export class AddArchivoCotizacionComponent implements OnInit {

  private fileTmp: any;
  fileUploadForm: FormGroup;
  doctors: CotizacionModel;
  id_patient;

  constructor(private RestService: RestService, private fb: FormBuilder,
    private ActivatedRoute: ActivatedRoute,
    @Inject(MAT_DIALOG_DATA) public data: any,) {

    this.doctors = data.doctors;
    const id = this.ActivatedRoute.snapshot.params.id;

    this.fileUploadForm = fb.group({
      singleUpload: ["",[Validators.required]],
      name: ["",[Validators.required]],
    });

  }

  ngOnInit(): void {

  }

  getFile($event: any) {
    const [file] = $event.target.files;
    console.log(file);
    this.fileTmp = {
      fileRaw: file,
      fileName: file.name
    }
  }

  sendFile() {

    const id = this.data.id_patient
    const name: string = this.fileUploadForm.value['name'];
    console.log(name);
    const body = new FormData();
    body.append('myFile', this.fileTmp.fileRaw, this.fileTmp.fileName);
    // body.append('email', 'test@test.com');
    body.append('name', name);
    console.log(this.data.id_patient);
    this.RestService.sendPost(body)
      .subscribe(res => console.log(res))

  }




}
